.card {
 
  border-radius: 4px;
  height: 170px;
  margin-left: auto;
  margin-bottom: 10px;
  /* position: relative; */
  width: 200px;
  box-shadow: inset 0 0 3px 2px #999, 0 3px 6px rgb(77, 75, 75);
  
}

.card > .img-thumbnail {
  height: 100%;
  overflow: hidden;

}
.card > .img-thumbnail > img {
  width: 100%
}

.card > .content {
  padding: 1rem;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

img{
  height: 160px;
  width: 200px;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  /* STYLES HERE */
  img{
    height: 120px;
    width: 140px;
  }

  .card {
    height: 130px;
    width: 160px;
  }
  
}

@media screen and (max-width: 760px) {
  /* STYLES HERE */
  img{
    height: 89px;
    width: 90px;
  }

  .card {
    height: 100px;
    width: 120px;
  }

  span{
    font-size: 16px;
  }
  
  
}
.wrapper {
  padding-top: 30px;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

/* Makes body full size. */
body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: rgba(235, 233, 226, 0);
}
html {
  height: 100%;
}


.title {
  width: 50%;
  /* text-shadow: 3px 2px 4px rgba(137, 132, 123, 0.72); */
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.586), 0 0 1em rgba(128, 128, 128, 0.425), 0 0 0.2em rgba(211, 211, 211, 0.452);
  font-family: 'Wallpoet', cursive;
  color:rgba(236, 9, 9, 0.938);
}

span{
  width: 30%;
  float: right;
  font-size: 20px;
  font-family: 'Wallpoet', cursive;
  /* text-shadow: 3px 1px 2px rgba(137, 132, 123, 0.72); */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.586), 0 0 1em grey, 0 0 0.2em lightgrey;
}

#headline{
  box-shadow: 2px 2px 5px 4px rgba(29, 28, 28, 0.449);
  opacity: 0.7;

}

#top{
  margin-top: 40px;
}

#root{
  background-image: url(/clickgame/static/media/sky.5d5fea62.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.9;
}

#inst{
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  margin-top: -8px;
  margin-left: auto;
  margin-right: auto;
  border: solid 2px rgba(0, 0, 0, 0.87);
  padding-left: 2px;
  padding-right: 2px;
  background-color: rgba(255, 251, 0, 0.651);
}

#spacer{
  height:40px ;
}

@media screen and (max-width: 660px) {
  span{
    font-size: 10px !important;
  }
  .title{
    font-size: 18px !important;
  }
#inst{
  font-size:11px;
}
}

@media screen and (max-width: 760px) {
  span{
    font-size: 12px;
  }
  .title{
    font-size: 24px;
  }
}

@media screen and (max-width: 930px) {
  span{
    font-size: 14px;
  }
  .title{
    font-size: 26px;
  }
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

